import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
// import Cart from "./Cartqty";
import { Link, useParams } from "react-router-dom";
import axios from "axios";

import { addToCart } from "../slices/cartSlice";
import { useGetAllProductsQuery } from "../slices/productsApi";

const Cart2 = () => {
  // const { items: products, status } = useSelector((state) => state.products);
  const { items: products, status } = useSelector((state) => state.products);
  const dispatch = useDispatch();
  const history = useHistory();
  const [item, setItem] = useState({});

  const { id } = useParams();
  useEffect(() => {
    const loadUser = async () => {
      //const res = await axios.get(`http://localhost:3003/users/${id}`);
      // https://fakestoreapi.com/products

      // const res = await axios.get(`http://localhost:4000/users/${id}`);

      const res = await axios.get(
        `https://fakewoocomapi.github.io/products/${id}`
      );
      setItem(res.data);
    };

    loadUser();
  }, []);

  const handleAddToCart = (product) => {
    dispatch(addToCart(product));
    history.push("/cart");
    // history.push("/details");
  };

  return (
    <div className="container py-4">
      <Link className="btn btn-primary" to="/">
        back to Home
      </Link>
      <h1 className="display-4">Product Id: {item.id}</h1>
      <hr />

      <div className="container">
        <div className="row">
          <div className="col-md-6 col-12 text-center">
            <img className="img-fluid" src={item.image} alt="abcdgh" />
          </div>

          <div className="col-md-6 col-12">
            <h2 className="display-2">Product Id: {item.id}</h2>
            <h3 className="list-group-item">
              <strong className="text-success"> Name:</strong> {item.name}
            </h3>

            <div className="list-group-item">
              <strong> Price</strong> {item.price}
            </div>
            <div className="list-group-item">user name: {item.description}</div>
            <div>{/* <Cart /> */}</div>
            <button
              className="btn btn-success"
              onClick={() => handleAddToCart(item)}
            >
              Add To Cart
            </button>
            {/* <div className="list-group-item">phone: {user.phone}</div>
  <div className="list-group-item">website: {user.website}</div> */}
            <div className="continue-shopping">
              <Link to="/">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  fill="currentColor"
                  className="bi bi-arrow-left"
                  viewBox="0 0 16 16"
                >
                  <path
                    fillRule="evenodd"
                    d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z"
                  />
                </svg>
                <span>Continue Shopping</span>
              </Link>
            </div>
            <br />
            <hr />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Cart2;
